export default function selectionFilter({ series, films } = []) {
  return {
    series: [
      { title: '', data: series?.filter((item) => item.genre === 'documentaries') },
      { title: '', data: series?.filter((item) => item.genre === 'comedies') },
      { title: '', data: series?.filter((item) => item.genre === 'children') },
      { title: '', data: series?.filter((item) => item.genre === 'crime') },
      { title: ' ', data: series?.filter((item) => item.genre === 'feel-good') },
    ],
    films: [
      { title: '', data: films?.filter((item) => item.genre === 'drama') },
      { title: '', data: films?.filter((item) => item.genre === 'thriller') },
      { title: '', data: films?.filter((item) => item.genre === 'children') },
      { title: '', data: films?.filter((item) => item.genre === 'suspense') },
      { title: '', data: films?.filter((item) => item.genre === 'romance') },
    ],
  };
}
