import React from 'react'
import { BrowserRouter as Router, Switch } from 'react-router-dom';
import { Home,Browse ,SignIn, SignUp} from './pages';
import * as ROUTES  from "./constants/routes";
import { useAuthListener } from './hooks';
import { IsUserRedirect, ProtectedRoute } from './helpers/routes';
 
export default function App() {
  console.log(" ");
  console.log("  2.0")
  console.log("  ")
  //  useAuth  
   const { user } = useAuthListener();

  return (
    <Router>
       <Switch>
         {/*         Browse   SignIn .*/}
         <IsUserRedirect 
         user={user} 
         loggedInPath={ROUTES.BROWSE} 
         path={ROUTES.HOME}
         exact
         >
          <Home />
        </IsUserRedirect>

        <IsUserRedirect
           user={user}
           loggedInPath={ROUTES.BROWSE}
           path={ROUTES.SIGN_UP}
        > 
        <SignUp />
        </IsUserRedirect>

        <IsUserRedirect
          user={user}
          loggedInPath={ROUTES.BROWSE}
          path={ROUTES.SIGN_IN}
          >
          <SignIn />
          </IsUserRedirect> 


        <ProtectedRoute 
        user={user} 
        path={ROUTES.BROWSE}
        >
          <Browse />
        </ProtectedRoute>          
       </Switch>
    </Router>
  );
}


 


 