import axios from "axios";
import values from "./values";


// apiConfig     json type  Type .

const apiConfig = {
    baseURL:"http://api.themoviedb.org/3",
    responseType: "json",
}


const omdbApiConfig = {
    baseURL: "http://www.omdbapi.com",
    responseType: "json",
}


const API_KEY = values.api_key;

// API  
