import React, { useContext, useState,useCallback} from 'react';
import { HeaderContainer } from '../containers/header';
import { Form } from "../components";
import { FirebaseContext } from '../context/firebase';
import { useHistory } from 'react-router-dom';
import * as ROUTES from '../constants/routes';
import { FooterContainer } from '../containers/footer';


export default function  SignIn() {
  //     
  const history = useHistory();
  
  const { firebase } = useContext(FirebaseContext);

  //  const PASSWORD = 'password';

    const [emailAddress, setEmailAddress] = useState('');
    // const [passwordType, setPasswordType] = useState(PASSWORD);
    const [errorMessage, setErrorMessage] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
  
    

    //    &&     
    // const isInvalid = password === '' || emailAddress === ''; 

    //    == form  
    
    const handleSignin = (event) =>  {
      event.preventDefault();

       //   auth  Email Password  
       //  history  Router  Browse  
       firebase
         .auth()
         .signInWithEmailAndPassword(emailAddress,password)
         .then(() => {
             setEmailAddress("");
             setPassword("");
             history.push(ROUTES.BROWSE)
         })
         // 
         //        
         .catch((error) => {
           if (error.code === 'auth/invalid-email') {
             setErrorMessage('    ');
           } else if (error.code === 'auth/user-disabled') {
             setErrorMessage('   .') 
             
           } else if (error.code === 'auth/user-not-found') {
              setErrorMessage('    .');
           } else if (error.code === 'auth/wrong-password') {
              setErrorMessage('  .  ');
           }
          setError(error.message);
        });     
    }

  
    



    //   
    return (
       <>
         <HeaderContainer>
            <Form>
              <Form.Title></Form.Title>
      
              {errorMessage && <Form.Error data-testid="error">{errorMessage}</Form.Error>}
              
              <Form.Base onSubmit={handleSignin} method="POST">
                 <Form.Input 
                    placeholder=" "  
                    type="text"
                    value={emailAddress}
                    onChange={({target}) => setEmailAddress(target.value)} 
     
                />

                <Form.Input 
                   type="password"
                   value={password}
                   autoComplete="off"
                   placeholder=""
                   onChange={({target}) => setPassword(target.value)}
                   
                />

                <Form.Submit type="submit" data-testid="sign-in" >
                    
                </Form.Submit>
              </Form.Base>
              <Form.Text>
                Netflix  ? <Form.Link to="/signup"> </Form.Link>
              </Form.Text>
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </HeaderContainer>
         <FooterContainer />
       </>  
    )
}